@keyframes hand-wave-animation {
  0% {
    transform: rotate(0deg); }
  10% {
    transform: rotate(-10deg); }
  20% {
    transform: rotate(12deg); }
  30% {
    transform: rotate(-10deg); }
  40% {
    transform: rotate(9deg); }
  50% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(0deg); } }

@keyframes newWord {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

