.projects {
  background: $light-gray;
  padding: 8rem 0px 4.5rem;

  &__title {
    max-width: $max-width;
    font-weight: 600;
    font-size: 2.25rem;
    line-height: 3rem;
    padding: 1rem 0;
  }

  &__wrapper {
    max-width: $max-width;
    margin: 0 auto;
    padding: 0 16px;
  }

  &__description {
    color: $gray;
    font-weight: 300;
    font-size: 1.25rem;
    line-height: 2.25rem;
    max-width: 580px;
  }

  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 24px;
    column-gap: 24px;
    // align-items: baseline;
    @media (max-width: $resMobile) {
      grid-template-columns: 1fr;
    }
  }

  &__link {
    color: $blue;
    font-weight: 500;
    @include link-hover-animation();
  }
}

.project {
  background: white;
  max-width: 580px;
  min-width: 280px;
  min-height: 300px;
  border-radius: 8px;
  box-shadow: 0px 6px 16px rgba(24, 41, 67, 0.09);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  transition: box-shadow $short-animation ease-in-out;
  @media (max-width: $resMobile) {
    max-width: 100%;
  }
  &:hover {
    box-shadow: 0px 12px 32px rgba(24, 41, 67, 0.16);
  }

  &__updated {
    font-weight: 300;
    font-size: 1.125rem;
    margin-right: 48px;
    margin-bottom: 0;
    position: absolute;
    top: 0;
    right: 0;
    cursor: help;
    @media (max-width: $resMobile) {
      margin-right: 32px;
    }
  }

  &__content {
    padding: 48px;
    @media (max-width: $resMobile) {
      padding: 32px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 2.25rem;
    margin: 1rem 0 0.5rem;
  }

  &__description {
    font-weight: 300;
    font-size: 1.125rem;
    line-height: 2rem;
    
  }

  &__footer {
    background: $light-gray;
    padding: 1rem 3rem;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: right;
  }

  &__link {
    color: $blue;
    font-weight: 500;
    font-size: 1.125rem;
    line-height: 2rem;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    & + & {
      margin-left: 50px;
      position: relative;
      &::after {
        content: '';
        height: 2rem;
        width: 2px;
        background: #CBD2D9;
        display: block;
        position: absolute;
        left: -26px;
        pointer-events: none;
      }
    }
    &::before {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
      background-image: url('/assets/img/demo.svg'); 
      margin-right: 8px;
    }
    &--code::before {
      background-image: url('/assets/img/source.svg'); 
    }
  }
}